h1,
h2,
h3,
h4 {
  text-align: center;
  font-weight: bolder;
}
h1 {
  font-size: 3rem;
  margin: 1rem;
}
h2 {
  font-size: 2rem;
  margin-top: 1rem;
  margin-bottom: 0.5rem;
}
h3 {
  font-size: 1, 8rem;
  margin: 0;
}
small {
  font-size: 0.8rem;
}
.topNews {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}
.News {
  background-color: #f0f0f0;
  max-width: 300px;
  padding: 1rem;
  margin: 1.5rem;
  -webkit-box-shadow: 5px 5px 7px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 5px 5px 7px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 5px 5px 7px 0px rgba(0, 0, 0, 0.75);
}
@media (min-width: 550px) {
  .News {
    max-width: none;
    width: 500px;
  }
}
@media (min-width: 1113px) {
  .News {
    height: 200px;
  }
}
.News:hover {
  -webkit-box-shadow: 10px 10px 10px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 10px 10px 10px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 10px 10px 10px 0px rgba(0, 0, 0, 0.75);
}
.News__header {
  display: flex;
  width: 100%;
  justify-content: space-between;
}
